.site-header {
    height: 171px;
    display: flex;
    justify-content: flex-end;
    padding: 0 !important;
}
.header-logo {
    margin-right: auto;
    align-self: center;
    margin-left: 2.5rem;
}
.header-nav, .header-phone {
    // margin-right: 1rem;
}
.header-nav, .header-phone, .header-request {
    align-self: flex-end;
}
.header-phone {
    a {
        display: block;
        color: #313036;
        font-size: 23px;
        padding: 1rem 1.7rem 3rem 0.85rem;
        transition: color 0.2s ease-in-out;
        &:hover {
            color: $secondaryColor;
        }
    }
}
.header-request {
    display: flex;
    align-self: stretch;
    a {
        display: block;
        align-self: stretch;
        padding: 5.65rem 4.5rem 0 4.5rem;
        text-decoration: none !important;
        font-size: 1.35rem;
        font-weight: 600;
        text-transform: uppercase;
        color: #fff !important;
        background-color: $secondaryColor;
        transition: all 0.2s ease-in-out;
        &:hover {
            background-color: $primaryColor;
        }
    }
}

.page-mast {
    position: relative;
    .mast-overlay {
        position: absolute;
        top: 50%;
        left: 10%;
        transform: translateY(-50%);
        text-align: center;
        color: #fff;
    }
}

.btn-page-mast {
    background-color: transparent;
    border: 2px solid #fff;
    color: #fff !important;
    font-size: 1.25rem;
    border-radius: 0;
    &:hover {
        background-color: rgba(white,0.2);
    }

}
